.modal_translate {
  max-height: 400px;
  overflow-y: scroll;
  background: #8bc34a;
  max-width: 400px;
  overflow-x: scroll;
  color: #3f51b5;
}

.modal_translate2 {
  border: 0px solid black;
  border-radius: 4px;
  bottom: auto;
  min-height: 5rem;
  left: 50%;
  padding: 2rem;
  position: fixed;
  right: auto;
  top: 50%;
  transform: translate(-50%, -50%);
  min-width: 20rem;
  width: 40%;
  max-width: 20rem;
  background: #eee;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}
